import '../styles/Main.css';
import { Link } from 'react-router-dom';

function Main() {
    return (
        <main className="mainbg">
            <section className="main-section" aria-label="Presentacin de la tienda de artes marciales">
                <h1 className="main-heading">Tienda de Artculos de Artes Marciales</h1>

                <div className="banner-promocion">
                    <h2 className="banner-title">POTENCI TU ENTRENAMIENTO HOY MISMO!</h2>
                    <p className="banner-text">
                        Descubr nuestras ofertas exclusivas en uniformes, protecciones y accesorios para artes marciales.
                    </p>
                    <Link
                        to="/productos"
                        className="btn-banner"
                        aria-label="Ir a la pgina de productos"
                    >
                        Ver productos disponibles
                    </Link>
                </div>
            </section>
        </main>
    );
}

export default Main;
